import React, { useState } from "react";

export default function EditExpenseForm({ expenseToEdit, onSave }) {
  const [editedExpense, setEditedExpense] = useState(expenseToEdit);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setEditedExpense((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handleSave = () => {
    onSave(editedExpense);
  };

  return (
    <div>
      <label>nhap lai ngay</label>
      <input
        type="text"
        name="ngay"
        value={editedExpense.ngay}
        onChange={handleChange}
      />
        <br></br>
        <label>nhap lai thng</label>
      <input
        type="text"
        name="thang"
        value={editedExpense.thang}
        onChange={handleChange}
      /> 
      <br></br>
      <label>chn li danh mc</label>
      <select
        name="danhMuc"
        value={editedExpense.danhMuc}
        onChange={handleChange}
      >
        <option value="">Chn danh mc</option>
        <option value="n ung">n ung</option>
        <option value="Hc hnh">Hc hnh</option>
        <option value="Gi tit kim">Gi tit kim</option>
      </select>

      <br></br>
      <label>nhp li s tin</label>
      <input
        type="text"
        name="soTien"
        value={editedExpense.soTien}
        onChange={handleChange}
      />
      <br></br>
      <label>Vit li ni dung</label>
      <input
        type="text"
        name="noiDung"
        value={editedExpense.noiDung}
        onChange={handleChange}
      />
      <button onClick={handleSave}>Lu</button>
    </div>
  );
}




